header {
  width: 100%;
  background: center / cover no-repeat url('../img/bg-header.jpg');
}
body {
  background-color: #f6f6f2;
}
.bg-darker {
  background-color: rgba(0, 0, 0, 0.616);
  display: block;
  width: 100%;
  height: 100%;
}
.container { 
  margin: 0 auto;
  width: 1200px;
}
.logo {
  display: block;
  width: 200px;
  height: 70px;
  background: center / contain no-repeat url('../img/WeAreWhite.png');
}
.main-menu {
  z-index: 666;
  position: fixed;
  width: 100%;
  height: 50px;
  background: black;
  .container {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    a {
      font-size: 18px;
      font-weight: 600;
      text-decoration: none;
      color: white;
      margin: 0 10px;
      cursor: pointer;
      &:hover {
        color: gold;
      }
    }
  }
}
.number-logo {
  width: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 200px;
  a {
    font-size: 25px;
    font-weight: 600;
    text-decoration: none;
    color: white;
    margin: 0 10px;
    cursor: pointer;
    &:hover {
      color: gold;
    }
  }
}
h1 {
  font-size: 55px;
  font-weight: 900;
  color: white;
  margin-bottom: 40px;
}
.after-h1 {
  font-size: 30px;
  font-weight: 900;
  color: white;
  cursor: pointer;
  &:hover {
    color: gold;
  }
}
.recalling {
  padding: 40px 0;
  width: 550px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.number {
  width: 240px;
  height: 50px;
  border-radius: 30px;
  padding: 0 20px;
  border: none;
}
.submit {
  width: 200px;
  height: 50px;
  border-radius: 30px;
  padding: 0 20px;
  background: #f56e32;
  border: none;
}
.menu-show-hide {
  a {
    width: 100%;
    height: 100%;
    display: block;
    cursor: pointer;
  }
  &:nth-of-type(1) {
    width: 100px;
    height: 100%;
    background: center / contain no-repeat url('../img/WeAreWhite.png');
  }
}
.menu-show-hide-hide {
  display: none;
}
.menu-show-hide-show {
  display: block;
}

h2 {
  text-align: center;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 50px;
}
.why-we-greed {
  padding-bottom: 50px;
  display: grid;
  margin-top: 40px;
}
.why-we-element {
  h4 {
    text-align: center;
    font-size: 23px;
    font-weight: 600;
    margin-top: 190px;
  }
  p {
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    margin-top: 20px;
  }
  width: 300px;
  height: 250px;
  &:nth-of-type(1) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    background: top / contain no-repeat url('../img/why-1.png');
  }
  &:nth-of-type(2) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    background: top / contain no-repeat url('../img/why-2.png');
  }
  &:nth-of-type(3) {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    background: top / contain no-repeat url('../img/why-3.png');
  }
}

.repear-devices-grid {
  display: grid;
}
.repear-devices-element {
  cursor: pointer;
  &:hover {
    transform: scale(1.02);
  }
  p {
    text-align: center;
    font-size: 30px;
    font-weight: 600;
    margin-top: 20px;
  }
  border-radius: 50px;
  margin: 20px;
  display: block;
  &:nth-of-type(1) {
    p {
      margin-top: 80px;
    }
    width: 540px;
    height: 540px;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 3;
    background: right 190px / 300px 350px no-repeat url('../img/choice-iphone.png');
    background-color: white;
  }
  &:nth-of-type(2) {
    p {
      text-align: left;
      margin: 70px;
    }
    width: 540px;
    height: 250px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    background: right 20px / 330px 250px no-repeat url('../img/choice-ipad.png');
    background-color: white;
  }
  &:nth-of-type(3) {
    p {
      text-align: left;
      margin: 70px;
    }
    width: 540px;
    height: 250px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    background: right 20px / 330px 250px no-repeat url('../img/choice-mac-mini.png');
    background-color: white;
  }
}

.comford-grid {
  display: grid;
  justify-content: center;
  grid-gap: 30px;
}
.comfort-grid-element {
  p {
    text-align: center;
    font-size: 20px;
    font-weight: 600;
  }
  margin: 50px 0;
  width: 300px;
  height: 200px;
  &:nth-of-type(1) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    background: center 60% / 150px 100px no-repeat url('../img/parts.svg');
  }
  &:nth-of-type(2) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    background: center 60% / 150px 100px no-repeat url('../img/delivery.svg');
  }
  &:nth-of-type(3) {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    background: center 60% / 120px 100px no-repeat url('../img/delivery-man.svg');
  }
}

.sale-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: calc(100% - 80px);
  height: 220px;
  background: right 100% / 40% 100% no-repeat url('../img/promo-default.png');
  background-color: #54afff;
  border-radius: 50px;
  padding: 40px;
  h3 {
    font-size: 30px;
    font-weight: 600;
    color: white;
  }
  p {
    font-size: 20px;
    color: white;
  }
}

.questions {
  margin: 50px 0;
  position: relative;
  background-color: #f56e32;
  img {
    position: absolute;
    height: 450px;
    right: 0;
    top: -50px;
  }
}
.questions-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  h2 {
    color: white;
  }
  a {
    text-decoration: none;
    color: white;
    &:hover {
      color: gold;
    }
  }
}
.question-span {
  margin: 40px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 700px;
  color: white;
}